<template>
    <el-container direction="vertical">
        <div class="archives-header">
            <!--菜单栏nav-->
            <nav><MenuBar/></nav>
            <!--背景图片和字体-->
            <div class="archives-bg">
                <h1>归档</h1>
            </div>
        </div>
        <el-container class="archives-main">
            <div id="main-blog">
                <h1>共有{{articleList.value.length}}篇文章</h1>
                <el-row v-for="(value, key) in articleMap" :key="key">
                    <div>
                        <h1>{{value[0]}}</h1>
                    </div>
                    <el-col v-for="article in value[1]">
                        <el-card class="article-card">
                            <el-col>
                                {{article.articlePicture}}
                            </el-col>
                            <el-col>
                                {{article.articleTitle}}
                            </el-col>
                        </el-card>
                    </el-col>
                </el-row>
            </div>
            <div id="archives-card">
                <AsideBar/>
            </div>
        </el-container>
    </el-container>
</template>

<script>
import MenuBar from "@/components/MenuBar.vue";
import Article from "@/components/Article.vue";
import AsideBar from "@/components/AsideBar.vue";
import axios from "axios";
import {reactive} from "vue";
import {Map} from "core-js/internals/map-helpers";

export default {
    name: "Archives",
    components: {AsideBar, Article, MenuBar},
    setup() {

        const baseUrl = 'http://localhost:8000'
        // const baseUrl = 'http://119.91.22.108:8000'

        let articleList = reactive({
            value: [{
                articleId: 0,
                categoryId: 0,
                userId: 0,
                articleTitle: '',
                articleContent: '',
                articlePicture: '',
                deleteFlag: '',
                creationDate: '',
                lastUpdateTime: '',
                categoryName: ''
            }]
        })

        let articleMap = reactive(new Map())
        axios.get(
            baseUrl + '/article/selectAll'
        ).then(
            response => {
                articleList.value = response.data.data
                articleList.value.forEach(article => {
                    let currentYear = article.lastUpdateTime.substring(0, 4)
                    if (articleMap[currentYear] === undefined) {
                        articleMap[currentYear] = []
                    }
                    let articleArr = articleMap[currentYear]
                    articleArr.push(article)
                    articleMap.set(currentYear, articleArr)
                })
                console.log('articleMap', articleMap)
            }
        )

        return {
            articleList,
            articleMap
        }
    }
}
</script>

